#navbar ul{position:fixed;top:0;width:100%;list-style-type:none;margin:0;padding:0;overflow:hidden;background-color:transparent;z-index:1101}#navbar ul #nav-links{float:right;margin:10px}#navbar li{float:left;margin:2px}#navbar li #logo{float:left}#navbar li a{display:block;color:black;text-align:center;padding:14px 16px;text-decoration:none}#navbar li a:hover{background-color:#32936F !important;color:white}#navbar li.active>a{background-color:#2274A5;color:white}#navbar .button{padding-top:2px;border:none}



