h1{text-align:center;font-weight:600}h1.hero-heading{font-size:100px}a{color:#32936F}body{font-family:'Alegreya Sans', sans-serif;position:relative}::-moz-selection{text-shadow:none;background:rgba(232,63,111,0.25)}::selection{text-shadow:none;background:rgba(232,63,111,0.25)}.no-margin{margin:0}.align-right{text-align:right}.align-left{text-align:left}@-webkit-keyframes fadeIn{from{opacity:0}to{opacity:1}}@keyframes fadeIn{from{opacity:0}to{opacity:1}}.in-view{opacity:0;-webkit-animation:fadeIn ease-in 1;animation:fadeIn ease-in 1;-webkit-animation-fill-mode:forwards;animation-fill-mode:forwards;-webkit-animation-duration:1s;animation-duration:1s}.button{border-radius:100px;margin-bottom:0}.button-standalone{margin:0 1rem}section{padding:12rem 0;text-align:center}.section-even{background-color:#FFBF00;background-position:center;background-repeat:no-repeat}.section-heading,.section-description{margin-bottom:1.2rem}@media (min-width: 550px){.section{padding:12rem 0 11rem}.hero{padding-bottom:12rem;text-align:left;height:165px}.hero-heading{font-size:2.4rem}}@media (min-width: 750px){.hero{height:190px}.hero-heading{font-size:2.6rem}.section{padding:14rem 0 15rem}.hero{padding:16rem 0 14rem}.section-description{max-width:60%;margin-left:auto;margin-right:auto}.categories{padding:15rem 0 8rem}}@media (min-width: 1000px){.section{padding:20rem 0 19rem}.hero{padding:22rem 0}.hero-heading{font-size:3.0rem}}



